// =============================================================================
// FACILITATOR REGISTRY
// =============================================================================
// Central registry of facilitator configurations
// This is the source of truth for facilitator metadata

export interface FacilitatorRegistryEntry {
  id: string;
  name: string;
  baseUrl: string;
  networks: string[]; // CAIP-2 network IDs
  tokens: string[]; // Token symbols or addresses
  feeBps: number;
  priority: number;
}

/**
 * Facilitator ID type - all known facilitators
 * Use this union type for type-safe facilitator references
 */
export type FacilitatorId = 'cdp' | 'payai' | 'x402rs' | 'dexter' | 'cronos' | 'dummy';

/**
 * Facilitator registry
 * CDP is the primary facilitator for x402 payments
 */
export const FACILITATORS: Record<string, FacilitatorRegistryEntry> = {
  cdp: {
    id: 'cdp',
    name: 'CDP x402 Facilitator',
    baseUrl: process.env.CDP_FACILITATOR_URL || 'https://api.cdp.coinbase.com/platform/v2/x402',
    networks: ['eip155:8453', 'solana:mainnet', 'eip155:84532', 'solana:devnet'], // Base mainnet, Solana mainnet, Base sepolia, Solana devnet
    tokens: ['USDC'],
    feeBps: 0,
    priority: 100, // High priority (primary facilitator)
  },
  payai: {
    id: 'payai',
    name: 'PayAI Facilitator',
    baseUrl: process.env.PAYAI_FACILITATOR_URL || 'https://facilitator.payai.network',
    networks: ['eip155:1', 'eip155:8453', 'eip155:56', 'eip155:137', 'solana:mainnet'], // Multi-chain support
    tokens: ['USDC'],
    feeBps: 10, // Example: 0.1% fee (to be confirmed with PayAI)
    priority: 200, // Lower priority than CDP
  },
  /**
   * X402rs Facilitator
   * 
   * Rust-based x402 facilitator operated by the x402 community.
   * Supports Base (mainnet & testnet) and XDC networks.
   * 
   * Source: https://x402.org/networks
   */
  x402rs: {
    id: 'x402rs',
    name: 'X402rs Facilitator',
    baseUrl: process.env.X402RS_FACILITATOR_URL || 'https://facilitator.x402.rs',
    networks: [
      'eip155:84532',  // Base Sepolia (testnet)
      'eip155:8453',   // Base mainnet
      'xdc:50',        // XDC mainnet
    ],
    tokens: ['USDC'],
    feeBps: 0, // Community facilitator - no fees (to be confirmed)
    priority: 150, // Between CDP (100) and PayAI (200)
  },
  /**
   * Dexter Facilitator
   * 
   * Solana-native x402 facilitator operated by Dexter.
   * Public facilitator URL: https://facilitator.dexter.cash
   * Standard x402 endpoints: /verify and /settle
   * 
   * Supports Solana mainnet and devnet networks.
   */
  dexter: {
    id: 'dexter',
    name: 'Dexter Facilitator',
    baseUrl: process.env.DEXTER_FACILITATOR_URL || 'https://facilitator.dexter.cash',
    networks: [
      'solana:101',    // Solana mainnet (CAIP-2)
      'solana:mainnet', // Legacy alias
      'solana:102',    // Solana devnet (CAIP-2)
      'solana:devnet', // Legacy alias
    ],
    tokens: ['USDC'],
    feeBps: 0, // To be confirmed with Dexter
    priority: 120, // Between CDP (100) and X402rs (150) - strong Solana support
  },
  /**
   * Cronos x402 Facilitator
   * 
   * Official Cronos Labs x402 facilitator for Cronos network.
   * Base URL: https://facilitator.cronoslabs.org
   * Documentation: https://docs.cronos.org/cronos-x402-facilitator
   * 
   * Endpoints:
   * - Health: GET /healthcheck
   * - Verify: POST /v2/x402/verify
   * - Settle: POST /v2/x402/settle
   * - Discovery: GET /v2/x402/supported
   * 
   * Networks: "cronos" (mainnet, chain ID 25), "cronos-testnet" (testnet, chain ID 338)
   * Rate limits: 5 requests per minute per IP for settle/verify
   */
  cronos: {
    id: 'cronos',
    name: 'Cronos x402 Facilitator',
    baseUrl: process.env.CRONOS_FACILITATOR_URL || 'https://facilitator.cronoslabs.org',
    networks: [
      'eip155:25',     // Cronos mainnet (CAIP-2)
      'cronos',        // Legacy alias
      'eip155:338',    // Cronos testnet (CAIP-2)
      'cronos-testnet', // Legacy alias
    ],
    tokens: ['USDC', 'USDX'], // Cronos stablecoins (actual assets from /v2/x402/supported)
    feeBps: 0, // Cronos facilitator fee policy
    priority: 130, // Similar priority tier as community facilitators
  },
} as const;

/**
 * Get facilitator by ID
 */
export function getFacilitator(id: string): FacilitatorRegistryEntry | undefined {
  return FACILITATORS[id];
}

/**
 * Get all facilitators
 */
export function getAllFacilitators(): FacilitatorRegistryEntry[] {
  return Object.values(FACILITATORS);
}

/**
 * Get facilitators for a specific network
 */
export function getFacilitatorsForNetwork(network: string): FacilitatorRegistryEntry[] {
  return Object.values(FACILITATORS).filter((f) => f.networks.includes(network));
}

