// =============================================================================
// FACILITATOR HEALTH PROBES
// =============================================================================
// Low-volume health probes to monitor facilitator availability and performance
// Probes run at configured intervals and log results as route attempts

import { createRouteAttempt, createRoute } from '@/db/smf';
import { getDb } from '@/db/client';
import { createLogger } from '@/lib/logger';
import { FACILITATORS } from './facilitator-registry';
import type { PaymentRequirements } from './base-facilitator';

const logger = createLogger({ component: 'FacilitatorProbes' });

/**
 * Cache of probe route IDs per probe config
 * This ensures we reuse the same route entry for each probe config
 */
const probeRouteCache: Map<string, string> = new Map();

/**
 * Get or create a route entry for a probe
 * Probes need a valid route_id that references the routes table
 */
export async function getOrCreateProbeRoute(
  facilitatorId: string,
  network: string,
  token: string
): Promise<string> {
  const cacheKey = `${facilitatorId}-${network}-${token}`;
  
  // Check cache first
  if (probeRouteCache.has(cacheKey)) {
    return probeRouteCache.get(cacheKey)!;
  }

  // Create a route entry for this probe config
  // We'll use a deterministic approach: check if route exists, create if not
  try {
    const route = await createRoute({
      request_id: `probe-${cacheKey}`,
      correlation_id: null,
      client_id: 'smf-probe-system',
      agent_id: null,
      network: network,
      token: token,
      amount: '1', // Minimal amount for probes
      selected_facilitator_id: facilitatorId,
      status: 'planned', // Probes are always in 'planned' status
    });

    probeRouteCache.set(cacheKey, route.id);
    return route.id;
  } catch (error) {
    // If route already exists (race condition), try to find it
    logger.warn({ error, facilitatorId, network, token }, 'Failed to create probe route, trying to find existing');
    // Try to find existing route by request_id
    try {
      const db = getDb();
      const isPostgres = 'pool' in db || typeof (db as any).pool?.query === 'function';
      
      if (isPostgres) {
        const result = await (db as any).pool.query(
          `SELECT id FROM routes WHERE request_id = $1 LIMIT 1`,
          [`probe-${cacheKey}`]
        );
        if (result.rows.length > 0) {
          const routeId = result.rows[0].id;
          probeRouteCache.set(cacheKey, routeId);
          return routeId;
        }
      }
    } catch (queryError) {
      logger.error({ queryError }, 'Failed to query for existing probe route');
    }
    
    // Last resort: throw error - we need a valid route
    throw new Error(`Failed to create or find probe route for ${cacheKey}: ${error instanceof Error ? error.message : 'Unknown error'}`);
  }
}

/**
 * Probe configuration
 * Each probe defines a facilitator, network, token, and interval
 */
export interface ProbeConfig {
  facilitatorId: string;
  network: string; // CAIP-2 network ID
  token: string; // Token symbol or address
  intervalMs: number;
}

/**
 * Default probe configuration
 * Base: every 30 seconds
 * 
 * Note: Solana probe temporarily disabled - CDP does not support solana/USDC/x402
 * Re-enable when a supported Solana network/asset combo is confirmed
 */
export const PROBE_CONFIG: ProbeConfig[] = [
  {
    facilitatorId: 'cdp',
    network: 'eip155:8453', // Base mainnet
    token: 'USDC',
    intervalMs: 30_000, // 30 seconds
  },
  // Temporarily disabled - CDP does not support solana/USDC/x402
  // {
  //   facilitatorId: 'cdp',
  //   network: 'solana:mainnet',
  //   token: 'USDC',
  //   intervalMs: 120_000, // 2 minutes
  // },
];

/**
 * Rate limit cooldown state per facilitator/network
 */
interface RateLimitState {
  facilitatorId: string;
  network: string;
  cooldownUntil: number; // Timestamp when cooldown expires
  currentInterval: number; // Current interval (may be increased due to rate limits)
}

const rateLimitStates: Map<string, RateLimitState> = new Map();

/**
 * Get rate limit state key
 */
function getRateLimitKey(facilitatorId: string, network: string): string {
  return `${facilitatorId}:${network}`;
}

/**
 * Check if facilitator/network is in cooldown
 */
function isInCooldown(facilitatorId: string, network: string): boolean {
  const key = getRateLimitKey(facilitatorId, network);
  const state = rateLimitStates.get(key);
  if (!state) return false;
  return Date.now() < state.cooldownUntil;
}

/**
 * Get current interval for a probe (may be increased due to rate limits)
 */
function getCurrentInterval(facilitatorId: string, network: string, defaultInterval: number): number {
  const key = getRateLimitKey(facilitatorId, network);
  const state = rateLimitStates.get(key);
  if (state && state.currentInterval > defaultInterval) {
    return state.currentInterval;
  }
  return defaultInterval;
}

/**
 * Handle rate limit response
 * Sets cooldown period and increases interval
 */
function handleRateLimit(facilitatorId: string, network: string, defaultInterval: number): void {
  const key = getRateLimitKey(facilitatorId, network);
  const cooldownMs = 5 * 60 * 1000; // 5 minutes cooldown
  const increasedInterval = defaultInterval * 2; // Double the interval
  
  rateLimitStates.set(key, {
    facilitatorId,
    network,
    cooldownUntil: Date.now() + cooldownMs,
    currentInterval: increasedInterval,
  });

  logger.warn(
    { facilitatorId, network, cooldownMs, newInterval: increasedInterval },
    'Rate limit detected, entering cooldown period'
  );
}

/**
 * Clear rate limit cooldown (called when probe succeeds)
 */
function clearRateLimit(facilitatorId: string, network: string, defaultInterval: number): void {
  const key = getRateLimitKey(facilitatorId, network);
  const state = rateLimitStates.get(key);
  
  // If we're in cooldown and get a success, gradually reduce interval back to default
  if (state && state.currentInterval > defaultInterval) {
    // Reduce by 25% each success, but don't go below default
    const newInterval = Math.max(defaultInterval, Math.floor(state.currentInterval * 0.75));
    rateLimitStates.set(key, {
      facilitatorId,
      network,
      cooldownUntil: 0,
      currentInterval: newInterval,
    });
  } else {
    // No cooldown, just ensure default interval
    rateLimitStates.set(key, {
      facilitatorId,
      network,
      cooldownUntil: 0,
      currentInterval: defaultInterval,
    });
  }
}

/**
 * Build minimal payment requirements for a probe
 * Uses tiny amount and test resource
 */
export function buildProbeRequirements(
  facilitatorId: string,
  network: string,
  token: string
): PaymentRequirements {
  const facilitator = FACILITATORS[facilitatorId];
  if (!facilitator) {
    throw new Error(`Facilitator ${facilitatorId} not found in registry`);
  }

  // Convert CAIP-2 network to legacy format if needed
  let legacyNetwork = network;
  if (network === 'eip155:8453') {
    legacyNetwork = 'base';
  } else if (network.startsWith('solana:')) {
    legacyNetwork = 'solana';
  }

  // Get token address for Base (USDC)
  let tokenAddress = token;
  if (network === 'eip155:8453' && token === 'USDC') {
    tokenAddress = '0x833589fCD6eDb6E08f4c7C32D4f71b54bdA02913';
  }

  return {
    scheme: 'x402',
    network: legacyNetwork,
    asset: tokenAddress,
    maxAmountRequired: '1', // Minimal amount (1 wei/lamport)
    resource: `https://probe.nexflow.dev/health/${facilitatorId}/${network}/${token}`,
    description: `Health probe for ${facilitatorId} on ${network}`,
    mimeType: 'application/json',
    payTo: facilitator.baseUrl, // Use facilitator base URL as payTo
    maxTimeoutSeconds: 10,
    settlementMode: 'immediate',
  };
}

/**
 * Run a single probe
 */
async function runProbe(config: ProbeConfig): Promise<void> {
  const { facilitatorId, network, token, intervalMs } = config;

  // Check if in cooldown
  if (isInCooldown(facilitatorId, network)) {
    logger.debug({ facilitatorId, network }, 'Skipping probe - in rate limit cooldown');
    return;
  }

  try {
    // Lazy load router to avoid importing CDP dependencies at module load time
    const { getFacilitatorRouter } = await import('./facilitator-router');
    const router = getFacilitatorRouter();
    const facilitator = router.getFacilitators().find((f) => f.id === facilitatorId);
    
    if (!facilitator) {
      logger.warn({ facilitatorId }, 'Facilitator not found in router, skipping probe');
      return;
    }

    // Build minimal requirements for probe
    const requirements = buildProbeRequirements(facilitatorId, network, token);

    // Create a minimal test payment header for connectivity testing
    // Note: This may result in invalid_request errors due to format/encoding issues,
    // but that's acceptable - we classify these correctly as invalid_request, not success
    // TODO: Align probe payment header format with working CDP verify flow for actual success
    const probePayment = `x402: version=2, scheme=x402, network=${network}, token=${token}, amount=1`;

    const startTime = Date.now();
    let result: 'success' | 'failure' | 'timeout' | 'rate_limited' | 'invalid_request' | 'network_error' = 'success';
    let errorCode: string | null = null;
    let rawStatus: number | null = null;

    try {
      // Attempt verification (this will likely fail with invalid payment, but tests connectivity)
      const verifyResult = await facilitator.verify(probePayment, requirements);
      const latency = Date.now() - startTime;

      // Determine result based on actual verification outcome
      if (verifyResult.success && verifyResult.valid) {
        result = 'success';
        clearRateLimit(facilitatorId, network, intervalMs);
      } else {
        // Classify errors properly - don't treat all errors as "success"
        const err = (verifyResult.error || '').toLowerCase();
        
        if (err.includes('timeout') || err.includes('network') || err.includes('econnrefused')) {
          result = 'network_error';
        } else if (
          err.includes('invalid json') || 
          err.includes('invalid payment') ||
          err.includes('malformed') ||
          err.includes('parse error')
        ) {
          result = 'invalid_request';
        } else if (
          err.includes('does not support') ||
          err.includes('unsupported') ||
          err.includes('not supported')
        ) {
          result = 'invalid_request'; // Unsupported network/asset combo
        } else {
          // Other errors (authentication, server errors, etc.)
          result = 'failure';
        }
        errorCode = verifyResult.error || null;
      }

      // Get or create a route entry for this probe
      const probeRouteId = await getOrCreateProbeRoute(facilitatorId, network, token);
      
      // Log probe attempt
      await createRouteAttempt({
        route_id: probeRouteId,
        facilitator_id: facilitatorId,
        phase: 'verify',
        result,
        latency_ms: latency,
        error_code: errorCode,
        raw_status: rawStatus,
        is_probe: true,
      });

      logger.debug(
        { facilitatorId, network, token, result, latency },
        'Probe completed'
      );
    } catch (error) {
      const latency = Date.now() - startTime;
      const errorMessage = error instanceof Error ? error.message : 'Unknown error';

      // Determine error type with proper classification
      const errLower = errorMessage.toLowerCase();
      
      if (errLower.includes('timeout') || errLower.includes('timed out')) {
        result = 'timeout';
      } else if (errLower.includes('rate') || errLower.includes('429') || errLower.includes('too many requests')) {
        result = 'rate_limited';
        rawStatus = 429;
        handleRateLimit(facilitatorId, network, intervalMs);
      } else if (errLower.includes('network') || errLower.includes('econnrefused') || errLower.includes('connection')) {
        result = 'network_error';
      } else if (
        errLower.includes('invalid json') ||
        errLower.includes('invalid payment') ||
        errLower.includes('malformed') ||
        errLower.includes('parse error')
      ) {
        result = 'invalid_request';
      } else if (
        errLower.includes('does not support') ||
        errLower.includes('unsupported') ||
        errLower.includes('not supported')
      ) {
        result = 'invalid_request'; // Unsupported network/asset combo
      } else {
        result = 'failure';
      }

      errorCode = errorMessage;

      // Get or create a route entry for this probe
      const probeRouteId = await getOrCreateProbeRoute(facilitatorId, network, token);
      
      // Log failed probe attempt
      await createRouteAttempt({
        route_id: probeRouteId,
        facilitator_id: facilitatorId,
        phase: 'verify',
        result,
        latency_ms: latency,
        error_code: errorCode,
        raw_status: rawStatus,
        is_probe: true,
      });

      logger.warn(
        { facilitatorId, network, token, result, error: errorMessage },
        'Probe failed'
      );
    }
  } catch (error) {
    logger.error(
      { error, facilitatorId, network, token },
      'Probe execution error'
    );
  }
}

/**
 * Start probe scheduler for a single probe config
 */
function startProbeScheduler(config: ProbeConfig): NodeJS.Timeout {
  const { facilitatorId, network, token, intervalMs } = config;

  logger.info(
    { facilitatorId, network, token, intervalMs },
    'Starting facilitator probe'
  );

  // Run immediately, then schedule
  runProbe(config).catch((error) => {
    logger.error({ error, facilitatorId, network, token }, 'Initial probe failed');
  });

  // Schedule recurring probes
  const interval = setInterval(() => {
    const currentInterval = getCurrentInterval(facilitatorId, network, intervalMs);
    
    // Check if we should skip due to cooldown
    if (isInCooldown(facilitatorId, network)) {
      logger.debug({ facilitatorId, network }, 'Skipping probe - in cooldown');
      return;
    }

    // Adjust interval if needed
    if (currentInterval !== intervalMs) {
      clearInterval(interval);
      // Restart with new interval
      startProbeScheduler({ ...config, intervalMs: currentInterval });
      return;
    }

    runProbe(config).catch((error) => {
      logger.error({ error, facilitatorId, network, token }, 'Scheduled probe failed');
    });
  }, intervalMs);

  return interval;
}

/**
 * Start all facilitator probes
 * Should be called on server startup in production only
 * Returns empty array if dependencies are missing (graceful degradation)
 * 
 * Note: This function should only be called when:
 * - NODE_ENV === 'production'
 * - ENABLE_FACILITATOR_PROBES === 'true'
 * The caller (instrumentation.ts) is responsible for these checks.
 */
export async function startFacilitatorProbes(): Promise<NodeJS.Timeout[]> {
  const env = process.env.NODE_ENV || 'development';
  
  // Double-check: never run in test environment
  if (env === 'test') {
    logger.info('Skipping facilitator probes in test environment');
    return [];
  }

  // Log probe configuration
  logger.info({
    env,
    probeCount: PROBE_CONFIG.length,
    probes: PROBE_CONFIG.map((c) => ({
      facilitator: c.facilitatorId,
      network: c.network,
      token: c.token,
      intervalSeconds: c.intervalMs / 1000,
    })),
  }, 'Starting facilitator probes');

  // Check if required dependencies are available
  // Try to dynamically import to verify dependencies exist
  try {
    // This will throw if dependencies are missing
    await import('@coinbase/cdp-sdk/auth');
    await import('viem');
    logger.info('Required dependencies (@coinbase/cdp-sdk/auth, viem) are available');
  } catch (error) {
    logger.warn(
      { error: error instanceof Error ? error.message : 'Unknown error' },
      'Facilitator probes disabled - required dependencies (@coinbase/cdp-sdk/auth, viem) not available'
    );
    return [];
  }

  // Start all probe schedulers
  const intervals = PROBE_CONFIG.map((config) => {
    try {
      return startProbeScheduler(config);
    } catch (error) {
      logger.error(
        { error, config },
        'Failed to start probe scheduler for config'
      );
      // Continue with other probes even if one fails
      return null;
    }
  }).filter((interval): interval is NodeJS.Timeout => interval !== null);

  logger.info(
    { started: intervals.length, total: PROBE_CONFIG.length },
    'Facilitator probes started'
  );

  return intervals;
}

/**
 * Stop all facilitator probes
 */
export function stopFacilitatorProbes(intervals: NodeJS.Timeout[]): void {
  intervals.forEach((interval) => clearInterval(interval));
  logger.info('Stopped all facilitator probes');
}

